import {Component, OnInit} from '@angular/core';
import {Http} from '@angular/http';
import {ReportJson} from './entity/report-json';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit {

    title = 'app works!';
    cn: any;
    reportJson: ReportJson;

    constructor(private http: Http) {
    }

    ngOnInit() {
        this.reportJson = new ReportJson;
        this.reportJson.beginTime = new Date();
        this.reportJson.beginTime.setHours(8);
        this.reportJson.beginTime.setMinutes(0);
        this.reportJson.beginTime.setSeconds(0);
        this.reportJson.beginTime.setMilliseconds(0);
        this.reportJson.endTime = new Date(this.reportJson.beginTime);
        this.reportJson.endTime.setDate(this.reportJson.beginTime.getDate() - 1);
        this.cn = {
            firstDayOfWeek: 0,
            dayNames: ['', '', '', '', '', '', ''],
            dayNamesShort: ['', '', '', '', '', '', ''],
            dayNamesMin: ['', '', '', '', '', '', ''],
            monthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
            monthNamesShort: ['', '', '', '', '', '', '', '', '', '', '', '']
        };
    }

    query() {
        this.http.post('getShenBeiTiCheng', this.reportJson)
            .subscribe((i) => {
                window.open('/receipt/' + i.json());
            });
    }
}



